body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  padding:0;
  margin:0;
}
.App{
}


.header{
    height: 50px;
    width:100%;
    background:white;
    color:gray;
    padding:0 50px;
    box-sizing: border-box;
    box-shadow: 1px 2px 5px rgba(0,0,0,0.08);
    margin: 0 0 2px 0;

    display:flex;
    justify-content: flex-end;
    align-items: center;
}

.header #menu{
    list-style: none;
    background:transparent;
    width:150px;
    padding:5px;
    box-sizing: border-box;

    display: flex;
    justify-content: space-around;
    align-items: center;
}

.header #menu .menu-item{
    display:inline-block;
    color:gray;
    padding:5px 10px;
    box-sizing: border-box;
    border-bottom:2px solid transparent;
}

.header #menu .menu-item:hover{
    border-bottom:2px solid rebeccapurple;

    transition: all 0.5s linear;
    -webkit-transition: all 0.5s linear;
    -moz-transition: all 0.5s linear;
    -ms-transition: all 0.5s linear;
    -o-transition: all 0.5s linear;
}

.ctr-content{
    height: 100vh;
    width:100%;
    background: rgba(0,0,0,0.05);
   /*padding:10px;*/
    box-sizing: border-box;
    
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer{
    width:100%;
    height: 100px;
    background:white;
    color:gray;

    display: flex;
    justify-content: center;
    align-items: center;
}
#ctr-login-form{
    background: white;
    padding:10px;
    box-sizing: border-box;
    width:450px;
    height:250px;
    border:0.5px solid rgba(0,0,0,0.1);
    border-radius: 3px;
    box-shadow: 1px 2px 10px rgba(0,0,0,0.2);

    display: flex;
    flex-wrap: wrap;
    justify-content:flex-start;
    align-items: center;
}

#ctr-login-form #title{
    font-weight: 300;
    font-size:30px;
    color:gray;
}

#ctr-login-form #login-form{
    background: white;
    padding:10px;
    box-sizing: border-box;
    width:100%;
    height:70%;
    border:0.5px solid rgba(0,0,0,0.05);
    border-radius: 3px;

    display: flex;
    flex-wrap: wrap;
    justify-content:center;
    align-items: center;
}

#ctr-login-form #login-form .field{
    width:100%;
    height:35px;
    border:1px solid rgba(0,0,0,0.1);
    border-radius:3px;
    padding:0 5px;
    box-sizing: border-box;
    margin:0 0 5px 0;
    color:gray;
    outline-color:purple;
}

#ctr-login-form #login-form .btn{
 height: 30px;
 padding:0 15px;
 width:45%;
 margin:0 5px 0 0;
 border:none;
 border-radius:3px;
 cursor: pointer;
}

#ctr-login-form #login-form #btn-login{
    background: purple;
    color:white;   
    outline:none;
}

#ctr-login-form #login-form #btn-cancel{
    background: white;
    color:purple;
    border:1px solid purple; 
    outline:none;  
}
